import React from "react";
import { Button, Grid, MenuItem, OutlinedInput, Select } from "@material-ui/core";

// styles
import useStyles from "./styles";

// components
import Widget from '../../../components/Widget/Widget';
import { Typography } from '../../../components/Wrappers/Wrappers';
import DummyTable from "./DummyTable";
import PageTitle from "../../../components/PageTitle/PageTitle";

export default function Order() {
  const classes = useStyles();
  return (
    <>
      <PageTitle title="t nhp hng" />
      <Grid container spacing={4} className={classes.wrapper}>
        <Grid item xs={12} md={12}>
          <Widget disableWidgetMenu>

            <Grid container>
              <Grid item xs={12} md={6} lg={6}>
                <div className="filterblk">
                  <div className="ln">
                    <Typography variant="h6">Chn gi thu</Typography>
                    <Select
                      value="1"
                      input={
                        <OutlinedInput
                          labelWidth={0}
                        />
                      }
                      autoWidth
                    >
                      <MenuItem value="1">Gi thu A</MenuItem>
                      <MenuItem value="2">Gi thu B</MenuItem>
                      <MenuItem value="3">Gi thu C</MenuItem>
                    </Select>
                  </div>
                  <div className="ln">
                    <Typography variant="h6">Tm kim n t nhp hng</Typography>
                    <OutlinedInput
                      labelWidth={0}
                    />
                  </div>
                  <div className="ln">
                    <Typography variant="h6">ID phiu nhp</Typography>
                    <OutlinedInput
                      labelWidth={0}
                    />
                  </div>
                  <div className="ln">
                    <Typography variant="h6">Trng thi n t nhp hng</Typography>
                    <Select
                      value="1"
                      input={
                        <OutlinedInput
                          labelWidth={0}
                        />
                      }
                      autoWidth
                    >
                      <MenuItem value="1"> nhp</MenuItem>
                      <MenuItem value="2">Ch thanh ton</MenuItem>
                      <MenuItem value="3">Tr hng</MenuItem>
                    </Select>
                  </div>
                </div>
              </Grid>
              <Grid item xs={12} md={6} lg={6}>
                <div className="filterblk">
                  <div className="ln align-reverse">
                    <Button variant="contained" color="primary" component="span">
                      Ti ln n t nhp hng
                    </Button>
                  </div>
                  <div className="ln align-reverse">
                    <Button variant="contained" color="primary" component="span">
                      Ti ln n nhp hng
                    </Button>
                  </div>
                  <div className="ln align-reverse">
                    <Button variant="contained" color="primary" component="span">
                      Ti ln n tr hng
                    </Button>
                  </div>
                </div>
              </Grid>
            </Grid>
          </Widget>
        </Grid>
      </Grid>
      <Grid container spacing={4} className={classes.wrapper}>
        <Grid item xs={12} md={12}>
          <DummyTable />
        </Grid>
      </Grid>
    </>
  );
}
